<template>
  <div
    class="http_status"
    :style="is_remove ? 'display: none' : ''"
    v-loading="Loading"
  >
    <el-row align="middle" type="flex" class="name-id">
      <el-col :span="3"
        ><el-tooltip placement="bottom-start" class="name" :content="name">
          <div>{{ name }}</div>
        </el-tooltip>
        <div class="id no-line">{{ id }}</div></el-col
      >
      <el-col :span="6">
        <el-tooltip placement="bottom" class="url" :content="url">
          <div>{{ url }}</div>
        </el-tooltip>
      </el-col>
      <el-col :span="2"
        ><div class="status">
          <el-switch
            v-model="currentEnable"
            @click.native="switchEnable(currentEnable)"
            active-color="#13ce66"
            inactive-color="#ff4949"
          >
          </el-switch></div
      ></el-col>
      <el-col :span="8"
        ><div class="24h">
          <el-progress
            :text-inside="true"
            :stroke-width="24"
            :percentage="current24Status"
            :status="current24StatusStr"
            :format="format"
            :style="current24StatusTextColor"
          ></el-progress></div
      ></el-col>
      <el-col :span="2"
        ><div class="rate">{{ rate }}分钟</div></el-col
      >
      <el-col :span="3"
        ><div class="setting">
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            @click="dialogVisible = true"
          ></el-button></div
      ></el-col>
    </el-row>

    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
      <span>真的要删除监控吗</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="danger" @click="remove">删 除</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import axios from "axios";
import Qs from "qs";
export default {
  name: "http_status",
  props: {
    id: String,
    name: String,
    url: String,
    LastTime: String,
    enable: {
      type: Boolean,
      default: false,
    },
    status24: {
      type: Number,
      default: 98,
    },
    rate: {
      type: Number,
      default: 15,
    },
  },
  data: function () {
    return {
      Loading: false,
      is_remove: false,
      dialogVisible: false,
      currentEnable: this.enable,
      current24Status: this.status24,
      current24StatusStr:
        this.status24 >= 85
          ? "success"
          : this.status24 >= 25
          ? "warning"
          : "exception",
      current24StatusTextColor:
        this.status24 >= 50 ? "color: #fff;" : "color: #000",
    };
  },
  watch: {},
  methods: {
    format() {
      return this.current24Status + "% (最后更新:" + this.LastTime + ")";
    },
    remove: function () {
      this.dialogVisible = false;
      this.is_remove = true;
      axios({
        method: "post",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        url: window.host + "/http_monitor/remove",
        data: Qs.stringify({
          id: this.id,
        }),
      }).then(function () {});
    },
    switchEnable: function (val) {
      var self = this;
      this.Loading = true;
      axios({
        method: "post",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        url: window.host + "/http_monitor/edit",
        data: Qs.stringify({
          id: this.id,
          key: "enable",
          value: val,
        }),
      }).then(function () {
        self.Loading = false;
        self.$notify({
          title: "成功",
          message: "状态修改成功",
          type: "success",
        });
      });
    },
  },
};
</script>

