  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

#http .url {
  color: rgb(83, 186, 255);
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0 7%;
}

#http .name-id {
  padding-left: 10px;
}

#http .http_status {
  border-bottom: 1px solid rgb(247 247 247);
}

#http .el-row {
  text-align: center;
}

